import React from "react";
import { useState } from "react";
import * as axios from "axios";
import { useContext } from "react";
import { AuthContext } from "../../context/Auth-contex";
import "./AuthPage.css";

const AuthPage = () => {
  const auth = useContext(AuthContext);
  const [form, setForm] = useState({ email: "", password: "", status: "", name:'', surname: '' });
  const [sucReg, setSucReg] = useState(false)

  const changeHandler = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
  };

  const registherHandler = async () => {
    try {
      const data = await axios.post(
        "https://dry-headland-66248.herokuapp.com/api/register",
        {  ...form }
      );
      alert("Успешная регистрация")
    } catch (e) {}
  };
  const loginHandler = async () => {
    try {
      const data = await axios.post(
        "https://dry-headland-66248.herokuapp.com/api/login",
        { ...form }
      );
      auth.login(data.data.token, data.data.userId);
    } catch (e) {}
  };

  return (
    <div className="wrapper_auth_page">
      <div className="wrapper_right_side_login">
        <form className="form_login" action="">
          <input
            type="email"
            onChange={changeHandler}
            name="email"
            placeholder="Телефон или email"
          />
          <input
            type="password"
            onChange={changeHandler}
            name="password"
            placeholder="Пароль"
          />
          <div className="forgot_the_paswword">
            <button className ='but_reg_login' type="button" onClick={loginHandler}>
              Войти
            </button>
            <span>Забыли пароль?</span>
          </div>
        </form>
      </div>
      <div className="wrapper_right_side_rightster">
        <div className="text_top_wrapper_right_side_rightster">
          <span className ='first_time_in_vk_text'>Впервые ВКонтакте?</span>
          <span className ='momental_reg_text'>Моментальная регистрация</span>
        </div>
        <form className="form_login" action="">
        <input
            type="email"
            onChange={changeHandler}
            name="email"
            placeholder="Телефон или email"
          />
          <input
            type="password"
            onChange={changeHandler}
            name="password"
            placeholder="Пароль"
          />
          <input onChange={changeHandler} name ='name' placeholder ='Ваше имя' type="text"/>
          <input onChange={changeHandler} name ='surname' placeholder= 'Ваша фамилия' type="text"/>
          <button type ='button' onClick={registherHandler} className ='but_reg_auth'>Зарегистрироваться</button>
          
        </form>
        
      </div>
    </div>
  );
};

export default AuthPage;
