import { ProfileAPI } from "../API/api";

const SET_STATUS = "SET_STATUS";
const SET_NAME = "SET_NAME";
const SET_SURNAME = "SET_SURNAME";
const SET_POSTS = "SET_POSTS";

let initialState = {
  status: "",
  Posts: [],
  name: '',
  surName: '',
};

export const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_NAME:{
      return {
        ...state,
        name: action.name
      }
    }
    case SET_SURNAME:{
      return {
        ...state,
        surName: action.surName
      }
    }
    case SET_STATUS: {
      return {
        ...state,
        status: action.status,
      };
    }
    case SET_POSTS: {
      return {
        ...state,
        Posts: action.Posts
      };
    }
    default:
      return state;
  }
};

export const setStatus = (status) => {
  return {
    type: SET_STATUS,
    status,
  };
};
export const setName = (name) => {
  return {
    type: SET_NAME,
    name,
  };
};
export const setsurName = (surName) => {
  return {
    type: SET_SURNAME,
    surName,
  };
};
export const getName = (id) => {
  return (dispatch) => {
    ProfileAPI.getAPIProf(id).then((response) => {
      dispatch(setName(response.data.name));
    });
  };
};
export const getsurName = (id) => {
  return (dispatch) => {
    ProfileAPI.getAPIProf(id).then((response) => {
      dispatch(setsurName(response.data.surname));
    });
  };
};


export const setPosts = (Posts) => {
  return {
    type: SET_POSTS,
    Posts,
  };
};


export const getPosts = (userId) => {
  return (dispatch) => {
    ProfileAPI.getAPIPosts(userId).then((response) => {
      dispatch(setPosts(response.data.posts));
    });
  };
};
export const sendPost = (userId, title, date) => {
  return (dispatch) => {
    ProfileAPI.sendAPIPost(userId, title, date, false, null)
  };
};

export const deletePost = (id, title) => {
  return (dispatch) => {
    ProfileAPI.deleteAPIPost(id, title)
  };
};
export const setLikes = (id, visLike, likes, title, date) => {
  return (dispatch) => {
    ProfileAPI.setAPILikes(id, visLike, likes, title, date)
  };
};



export const getStatus = (id) => {
  return (dispatch) => {
    ProfileAPI.getAPIProf(id).then((response) => {
      dispatch(setStatus(response.data.status));
    });
  };
};

export const updateStatus = (userId, status) => {
  return (dispatch) => {
    ProfileAPI.updateAPIStatus(userId, status).then((response) => {
      dispatch(setStatus(status));
    });
  };
};
