import React, { useState, useContext } from "react";
import {getStatus, updateStatus, getName, getsurName} from  '../../../../../store/profile-reduser'
import Profile_about_top from "./Profile_about_top";

import * as axios from "axios";
import { connect } from "react-redux";
import { AuthContext } from "../../../../../context/Auth-contex";
import { useEffect } from "react";
const config = {
  headers: {
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS",
  },
};




const  Profile_about_top_cont = (props) => {
  let auth = useContext(AuthContext)
  useEffect(() =>{props.getStatus(auth.userId); props.getName(auth.userId); props.getsurName(auth.userId)}, [])
  
   
  return <Profile_about_top surName ={props.surName} name = {props.name} stat = {props.status} updateStatus ={props.updateStatus}/>;

}
let mapStateToProps = (state) =>{
 return{
    status: state.Profile.status,
    name: state.Profile.name,
    surName: state.Profile.surName
}
}
let mapDispatchToProps = {
  getStatus,
  updateStatus,
  getName,
  getsurName
}

export default connect(mapStateToProps, mapDispatchToProps)(Profile_about_top_cont);
