import React from "react";
import "./Profile_photo.css";

const Profile_photo = () => {
  return (
    <div className="profile_photo">
      <img
        src="https://vk.com/images/camera_200.png?ava=1"
        alt=""
      />
      <div className="flex_profile_photo">
        <button className="bt1_photo">Редактировать</button>
        <button className="bt2_photo">...</button>
      </div>
    </div>
  );
};
export default Profile_photo