import React from "react";
import "./Right_side_profile.css";
import Profile_about from "./Profile_about/Profile_about";

import Profile_photos_wall from "./Profile_photo_wall/Profile_photo_wall";
import Profile_posts_cont from "./Profile_posts/Profile_post_cont";


const Right_side_profile = (props) => {
  return (
    <div className="wrapper_profile_right">
      <Profile_about userId = {props.userId}/>
      <Profile_photos_wall />
      <Profile_posts_cont userId = {props.userId}/>
    </div>
  );
};

export default Right_side_profile;
