import React, { useState, createRef, useEffect } from "react";
import {getPosts, sendPost, deletePost, getName, getsurName, setLikes} from '../../../../store/profile-reduser'

import * as axios from 'axios'
import  {connect}  from "react-redux";
import Profile_posts from "./Profile_posts";



class Profile_posts_cont extends React.Component{
    componentDidMount(){
        this.props.getName(this.props.userId); 
        this.props.getsurName(this.props.userId)
    }
    render(){
        return (
            <Profile_posts surName = {this.props.surName}  name = {this.props.name} userId ={this.props.userId} setLikes ={this.props.setLikes} deletePost = {this.props.deletePost} getPosts = {this.props.getPosts} sendPost = {this.props.sendPost}  Posts = {this.props.Posts}/>
        )
    }
}


let mapStateToProps = (state) =>{
    return{
        Posts: state.Profile.Posts,
        name: state.Profile.name,
        surName: state.Profile.surName
    }
}

let mapDispatchToProps ={
    getPosts,
    sendPost,
    getName,
    getsurName,
    deletePost,
    setLikes
}


export default connect(mapStateToProps, mapDispatchToProps)(Profile_posts_cont);
