import React, { useState, createRef, useEffect } from "react";
import "./Profile_posts.css";
import Post from "./Post/Post";
import { deletePost } from "../../../../store/profile-reduser";


const Profile_posts = (props) => {
  let data = new Date();
  let send_data = data.toDateString() + ' ' + data.getHours() + ':' + data.getMinutes()
  let [appear, setAppear] = useState(false);

  function appearUp() {
    if (appear == false) {
      setAppear(true);

    }
    if (appear == true) {
      setAppear(false);
   
    }
  }
  function apperDownMouse(e) {
    if (appear == true) {
      if (e.currentTarget.value.length > 0) {
        return;
      } else {
        if (appear == true) {
          setAppear(false);
          e.currentTarget.value = "";
        }
      }
    }
  }
  let ref = React.createRef();
  function sendPost(title) {
    props.sendPost(props.userId, title, send_data)
  
  }
  let Post_info_main = props.Posts.map((p) => (
    <Post getPosts ={props.getPosts} name = {props.name} surName = {props.surName} userId ={props.userId} setLikes= {props.setLikes} date ={p.date} deletePost = {props.deletePost} info={p.title} likesCount={p.countLikes} liked={p.liked} id={p._id} />
  ));
  function newPostEnter(e) {
    if (e.keyCode === 13) {
      e.preventDefault();
      sendPost(ref.current.value)
      ref.current.value = ''
      setAppear(false);
    } else {
      return;
    }
  }
  function appearDown(e) {
    if (appear == true) {
      sendPost(ref.current.value)
      ref.current.value = ""

      setAppear(false);
    }
    if (appear == false){
      setAppear(true)
    }
  }
  useEffect(() =>{props.getPosts(props.userId)}, [props.Posts])
  return (
    <>
      <div
        className={
          (!appear && "wrapper_profile_posts_off") || "wrapper_profile_posts_on"
        }
      >
        <div className="enter_wrapper_post_profile">
          <img
            src="https://vk.com/images/camera_100.png?ava=1"
            alt=""
          />
          <textarea
            onMouseOut={apperDownMouse}
            onClick={appearUp}
            onKeyDown={newPostEnter}
            cols="10"
            ref={ref}
            rows="10"
            onSubmit=''
            className="post_enter_textarea"
            placeholder="Что у вас нового?"
          ></textarea>
        </div>
        {appear && (
          <button
            onClick={appearDown}
            className="publish_post_button"
            type="button"
          >
            Опубликовать
          </button>
        )}
      </div>
      <div>{Post_info_main}</div>
    </>
  );
};

export default Profile_posts;
